<template>
  <div>
    <!-- Loading  -->   
    <loading :active.sync="isLoading"></loading>

    <!--  -->
    <div class="text-right mt-4">
      <div class="btn btn-main" @click="openModal(true)"></div>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="150"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">{{item.origin_price | currency }}</td>
          <td class="text-right">{{item.price | currency}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
              <div class="btn-group">
                <div class="btn btn-outline-info btn-sm" @click="openModal(false, item)"></div>
                <div class="btn btn-outline-danger btn-sm" @click="delModal(item)"></div>
              </div>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination :pageNum="pagination" @getPage="getProducts" ></Pagination>
    <!-- Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input type="text" class="form-control" id="image" placeholder=""
                  v-model="tempProduct.imageUrl">
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i class="fas fa-cog fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control" ref="files"
                  @change="uploadFile">
                </div>
                <img :src="tempProduct.imageUrl"
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid" alt>   
              </div>
              
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title" placeholder=""
                  v-model="tempProduct.title">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input type="text" class="form-control" id="category" placeholder=""
                    v-model="tempProduct.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="unit" class="form-control" id="unit" placeholder=""
                    v-model="tempProduct.unit">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input type="number" class="form-control" id="origin_price" placeholder=""
                    v-model="tempProduct.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="number" class="form-control" id="price" placeholder=""
                    v-model="tempProduct.price">
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea type="text" class="form-control" id="description" placeholder=""
                  v-model="tempProduct.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea type="text" class="form-control" id="content" placeholder=""
                  v-model="tempProduct.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled"
                     v-model="tempProduct.is_enabled"
                    :true-value="1" :false-value="0">
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal -->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                 <strong class="text-danger">{{ tempProduct.title }}</strong> ()
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                <button type="button" class="btn btn-danger" @click="delProuduct()"></button>
            </div>
            </div>
        </div>
    </div>

  </div>
</template>


<script>
//ESLint  /* global $ */
import $ from "jquery";
import Pagination from "../Pagination";

export default {
  components:{
    Pagination,    
  },
  data() {
    return {
      products: [], //
      tempProduct: {}, //productModal
      isNew: false,
      isLoading: false, //Loading
      status: {
        fileUploading: false,
      },
      pagination: {}, 
    };
  },
  methods: {
    getProducts(page = 1) {
      //
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
      const vm = this;
      vm.isLoading = true; //loading 1
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.isLoading = false; //false

        vm.products = response.data.products;
        vm.pagination = response.data.pagination; //pagination 
      });
    },
    openModal(isNew, item) { //
      if(isNew){ //
          this.tempProduct = {};
          this.isNew = true;
      }else{
          this.tempProduct = Object.assign({}, item); //
          this.isNew = false;
      }
      $("#productModal").modal("show");
    },
    updateProduct(){
        let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
        let httpMethod = 'post';
        const vm = this;
        if(!vm.isNew){ // API
            api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
            httpMethod = 'put';
        }
        this.$http[httpMethod](api, {data: vm.tempProduct}).then(response => { //
            console.log(response.data);
            if(response.data.success){
                $("#productModal").modal("hide");
                vm.getProducts();//
            }else{
                alert('');
            }
        });
    },
    delModal(item){
        const vm = this;
        vm.tempProduct = Object.assign({}, item);
        $('#delProductModal').modal('show');
    },
    delProuduct(){
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        this.$http.delete(api, {data: vm.tempProduct}).then(response => { //
            console.log(response.data);
            if(response.data.success){
                $("#delProductModal").modal("hide");
                vm.getProducts();
            }else{
                alert('');
                $("#delProductModal").modal("hide");
            }
        });
    },
    uploadFile(){
      console.log(this);
      const uploadedFile = this.$refs.files.files[0]; //
      const vm = this;
      const formData = new FormData();//: 
      formData.append('file-to-upload', uploadedFile); //
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;

      vm.status.fileUploading = true; //loading 2

      this.$http.post(url, formData, { // FormData
        headers:{
          'Content-type': 'multipart/form-data'  
        },
      }).then((response) => {
        console.log(response.data);
        vm.status.fileUploading = false; //AJAXfalse

        if(response.data.success){
          // vm.tempProduct.imageUrl = response.data.imageUrl;
          // console.log(vm.tempProduct);
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);//
          this.$bus.$emit('message:push', '', 'success');
        }else{
          this.$bus.$emit('message:push', response.data.message, 'danger'); //bus
        }
      });
    },
  },
  created() {
    this.getProducts();
  }
};
</script>



