<template>
    <div>
        <loading :active.sync="isLoading" :is-full-page="true">
            <template>
                <CandleLoading/>
            </template>
        </loading>
        <div class="container py-5">
            <div class="row d-flex justify-content-center mt-5">
                <div class="col-12 col-md-6 col-lg-5" >
                    <div class="itemImg mt-4 mt-md-0" :style="{ backgroundImage: `url(${product.imageUrl})`}"></div>
                    <div class="mt-4 text-center text-md-left ">
                        <div class="d-md-none">
                            <h2 class="mt-4 mt-md-0 text-center text-md-left"><strong>{{ product.title }}</strong></h2>
                        </div>
                        <h5 class="text-info"><strong></strong></h5>
                        <hr>
                        <p>{{ product.content }}</p>
                        <p><br>{{ product.description }}</p>
                        <p>54 hr</p>
                        <p>2</p>
                        <p></p>
                        <p>300g10%</p>
                        <hr>
                        <p><strong></strong><br>0.5<br>
                            
                            
                        </p>
                        
                    </div>
                </div>
                <div class="col-12 col-md-6 col-lg-5">
                    <h2 class="mt-4 mt-md-0 text-center text-md-left"><strong>{{ product.title }}</strong></h2>
                    <div class="text-center text-md-left">
                        <span class="h3 text-info"><strong>NT {{ product.price | currency }}</strong></span>
                        <span class="h5 text-secondary ml-2"><del>NT {{ product.origin_price | currency }}</del></span>
                    </div>
                    <div class=" mt-3 text-center text-md-left">
                        <span class="h6 bg-text-light text-light border border-text-light py-1 px-2">9</span><span class="h6 border border-text-light py-1 px-2"> $999</span>
                        
                    </div>
                    <div class="mt-3 text-center text-md-left">
                        <span class="h6 bg-text-light text-light border border-text-light py-1 px-2">8</span><span class="h6 border border-text-light py-1 px-2 "> $1399</span>
                        
                    </div>
                    <div class="mt-4">
                        <label for="num" class="h6"></label>
                        <select name="" id="num" v-model="product.num" class="form-control" >
                            <option :value="num" v-for="num in 10" :key="num.id">
                                 {{ num }} {{ product.unit }}
                            </option>
                        </select>
                    </div>
                    <button class="btn btn-main mt-3 w-100" @click="addCart(product.id,product.num)"></button>
                    <hr>
                    <p class="text-center text-md-left "> 2~6 </p>
                    <p class="text-center text-md-left "></p>
                    <hr>    
                    <div class="text-center text-md-left ">
                        <h5 class="text-info"><strong></strong></h5>
                        <li class="text-text-light"><i class="fas fa-sun mr-2"></i>95</li>
                        <li class="text-text-light"><i class="fas fa-sun mr-2"></i>NT$50</li>
                    </div>
                    <hr>
                    <div>
                        <h5 class="text-center text-md-left text-info"><strong></strong></h5>
                        <table class="table">
                            <tr>
                                <th width="10%"></th>
                                <td width="30%"></td>
                            </tr>
                            <tr>
                                <th></th>
                                <td></td>
                            </tr>
                            <tr>
                                <th></th>
                                <td></td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>100% </td>
                            </tr>
                        </table>
                        
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<style lang="scss" scoped>
    .itemImg{
        height: 500px;
        width: 440px;
        background-size: cover;
        background-position: center center;
        margin: 0 auto;
    }
    li{
        list-style: none;
    }
    @media (max-width: 420px){
        .itemImg{
            width: 100%;
        }
    }
    @media (min-width: 768px) and (max-width: 1200px){
        .itemImg{
            width: 100%;
        }
    }
</style>

<script>

export default {
    data(){
        return{
            product: {},
            itemID: '',
            isLoading: false,
            
        }
    },
    methods: {
        getItemInfo(id){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
            vm.isLoading = true;
            vm.$http.get(api).then((response)=>{
                //console.log(response.data);
                vm.product = response.data.product;
                vm.isLoading = false;
            })
        },
        addCart(id,qty=1){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            const cart = {
                product_id: id,
                qty,
            }
            vm.$http.post(api,{ data: cart }).then((response)=>{
                //console.log(response.data);
                vm.$bus.$emit('updateCart');
                vm.$bus.$emit('message:push', response.data.message,'success');
            })
        },
    },
    created(){
        const vm = this;
        vm.itemID = vm.$route.params.itemID;//id
        vm.getItemInfo(vm.itemID);
    }
}
</script>


